<template>
    <div>
      <div class="page-title-box">
        <h4 class="page-title">App管理 </h4>
        <ol class="breadcrumb">
          <li>
            <button type="button" class="btn btn-success round" @click="dialogFormVisible = true,clearObj(entity)">新增</button>
          </li>

          <li >
            <button type="button" class="btn btn-danger round" @click="remove()">删除</button>
          </li>
          <li >
            <button type="button" class="btn btn-info round"  @click="reflesh()">刷新</button>
          </li>
        </ol>
        <div class="clearfix"></div>
      </div>

      <div class="row">
        <div class="col-md-12">
          <div class="white-box">
            <h2 class="header-title">App列表</h2>
            <div class="table-responsive">
              <template>
                <el-table
                  :data="tableData"
                  border
                  style="width: 100%"
                  size="mini"
                  @selection-change="changeFun"
                >
                  <el-table-column
                  type="selection"
                  width="50"
                  align="center">
                  </el-table-column>
                  <el-table-column
                    prop="rownum"
                    label="序号"
                    width="60"
                    align="center"
                  >
                  </el-table-column>
                  <el-table-column
                    prop="app_name"
                    label="APP名"
                    width="135"
                    align="center"
                  >
                  </el-table-column>
                  <el-table-column
                    prop="instruct"
                    align="center"
                    width="200"
                    label="APP指令">
                  </el-table-column>
                  <el-table-column
                    prop="founder"
                    align="center"
                    width="130"
                    label="创建人">
                  </el-table-column>
                  <el-table-column
                    prop="create_time"
                    align="center"
                    width="200"
                    label="创建时间"
                    :formatter="timeFormat">
                  </el-table-column>
                  <el-table-column
                    prop="agent_count"
                    label="授权Agent数"
                    width="100"
                    align="center"
                  >
                    <template slot-scope="scope">
                      <button type="button" class="btn btn-success outline-btn round" @click="dialogTableVisible=true">{{scope.row.agent_count}}</button>
                    </template>
                  </el-table-column>
                  <el-table-column
                    align="center"
                    label="操作"
                    show-overflow-tooltip>
                    <template slot-scope="scope">
                      <span>操作</span>
                    </template>
                  </el-table-column>
                </el-table>
              </template>
              <div style="text-align: center;">
                <el-pagination
                  background
                  layout="sizes,prev, pager, next,total"
                  :page-sizes="[10,20, 50, 100, 200]"
                  :total="page.totalRows"
                  :size="page.pageSize"
                  :current-page="page.currentPage"
                  @size-change="sizeChange"
                  @current-change="currentChange"
                >
                </el-pagination>
              </div>
            </div>
          </div>
        </div>
      </div>

      <el-dialog title="取消Agent授权" :visible.sync="dialogTableVisible">
        <el-container>
          <el-header>

            <el-row :gutter="20">
              <el-col :span="7">
                <el-input
                  size="small"
                  placeholder="ip"
                  v-model="entity.instruct">
                </el-input>
              </el-col>
              <el-col :span="7">
                <el-input
                  size="small"
                  placeholder="标识"
                  v-model="entity.instruct">
                </el-input>
              </el-col>
              <el-col :span="6">
                <button type="button" class="btn btn-success outline-btn round">
                  <i class="fa fa-search"></i>
                </button>
              </el-col>
            </el-row>

          </el-header>
          <el-main>
            <el-table :data="tableData">
              <el-table-column type="selection" width="50" align="center"></el-table-column>
              <el-table-column property="name" label="ip" width="200"></el-table-column>
              <el-table-column property="address" label="标识"></el-table-column>
            </el-table>
          </el-main>
        </el-container>
      </el-dialog>

      <el-dialog title="新增APP" :visible.sync="dialogFormVisible" :close-on-click-modal="false">
        <el-form :model="entity" status-icon :rules="rules" ref="form">
          <el-form-item label="APP指令:" :label-width="formLabelWidth" prop="instruct">
            <el-input v-model="entity.instruct" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="APP名:" :label-width="formLabelWidth" prop="app_name">
            <el-input v-model="entity.app_name" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="insert()">确 定</el-button>
        </div>
      </el-dialog>

    </div>
</template>

<script>
/* eslint-disable*/
import {formatDate} from '../date.js'
import {Map} from '../map.js'
import {isEmpty} from '../isEmpty.js'
import {rownum} from '../arrayUtil.js'

export default {
  name: 'AppManage',
  data () {
    return {
      page: this.config['page'],
      tableData: [],
      dialogFormVisible: false,
      formLabelWidth: '120px',
      dialogTableVisible: false,
      entity: {
        instruct: '',
        app_name: ''
      },
      selected: [],
      rules: {
        instruct: [
          { required: true, message: '请输入指令', trigger: 'blur' },
          { min: 1, max: 30, message: '长度在 1 到 30 个字符', trigger: 'blur' }
        ],
        app_name: [
          { required: true, message: '请输入app名', trigger: 'blur' },
          { min: 1, max: 30, message: '长度在 1 到 30 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  mounted: function () {
    this.queryListForPage()
  },
  methods: {
    queryListForPage: function (func) {
      this.$http.post(this.api['appQueryListForPage'], {page: this.page}).then(function (response) {
        let data = response.data
        if (data.code === 0) {
          rownum(data.data)
          this.tableData = data.data
          this.page = data.page
        } else if (data.code !== 204) {
          this.$alert(data.msg, '提示', {
            confirmButtonText: '确定'
          })
        }
        if (func) {
          func(response)
        }
      }, function () {
        this.$message.error('后台异常')
      })
    },
    sizeChange: function (size) {
      this.page.pageSize = size
      this.page.currentPage = 1
      this.queryListForPage()
    },
    currentChange: function (begin) {
      this.page.currentPage = begin
      this.queryListForPage()
    },
    timeFormat (row, column, cellValue) {
      if (isEmpty(cellValue)) {
        return ''
      }
      return formatDate(new Date(cellValue), 'yyyy-MM-dd HH:mm:ss')
    },
    insert: function(){
      let self = this
      self.$refs['form'].validate((valid) => {
        if (valid) {
          self.$http.post(this.api['appInsertOrUpdate'], this.entity).then(function (response) {
            let data = response.data
            if (data.code === 0) {
              self.dialogFormVisible = false
              self.queryListForPage()
            } else if (data.code !== 204) {
              self.$alert(data.msg, '提示', {
                confirmButtonText: '确定'
              })
            }
          }, function (error) {
            self.$message.error('后台异常')
          })
        }
      })
      },
    changeFun: function(val){
      this.selected = val
    },
    remove: function() {
      this.$http.post(this.api['appDelete'], this.selected).then(function (response) {
        let data = response.data
        if (data.code === 0) {
          this.$message({
            message: '成功',
            type: 'success'
          });
          this.queryListForPage()
        } else if (data.code !== 204) {
          this.$alert(data.msg, '提示', {
            confirmButtonText: '确定'
          })
        }
      }, function () {
        this.$message.error('后台异常')
      })
    },
    reflesh: function(){
      let self = this
      this.queryListForPage(function(){
        self.$message('刷新成功')
      })
    },
    clearObj: function (o) {
      if (o === undefined) {
        return
      }
      for (var p in o) {
        o[p] = ''
      }
      return o
    }
  }
}
</script>

<style scoped>

</style>



