<template>
  <div>
    <div class="page-title-box">
      <h4 class="page-title">Auth管理 </h4>
      <ol class="breadcrumb">
        <li>
          <button type="button" class="btn btn-success round" @click="dialogFormVisible = true,clearObj(entity)">新增</button>
        </li>

        <li >
          <button type="button" class="btn btn-danger round" @click="remove()">删除</button>
        </li>
        <li >
          <button type="button" class="btn btn-info round"  @click="reflesh()">刷新</button>
        </li>
      </ol>
      <div class="clearfix"></div>
    </div>

    <div class="row">
      <div class="col-md-12">
        <div class="white-box">
          <h2 class="header-title">Auth列表</h2>
          <div class="table-responsive">
            <template>
              <el-table
                :data="tableData"
                border
                style="width: 100%"
                size="mini"
                @selection-change="changeFun"
              >
                <el-table-column
                  type="selection"
                  width="50"
                  align="center">
                </el-table-column>
                <el-table-column
                  prop="rownum"
                  label="序号"
                  width="60"
                  align="center"
                >
                </el-table-column>
                <el-table-column
                  prop="auth_name"
                  label="authName"
                  width="135"
                  align="center"
                >
                </el-table-column>
                <el-table-column
                  prop="auth_id"
                  align="center"
                  width="200"
                  label="AuthID">
                </el-table-column>
                <el-table-column
                  prop="auth_key"
                  align="center"
                  width="200"
                  label="AuthKey">
                </el-table-column>
                <el-table-column
                  prop="create_time"
                  align="center"
                  width="200"
                  label="创建时间"
                  :formatter="timeFormat">
                </el-table-column>

                <el-table-column
                  align="center"
                  prop="app_name"
                  label="app名">
                </el-table-column>
              </el-table>
            </template>
            <div style="text-align: center;">
              <el-pagination
                background
                layout="sizes,prev, pager, next,total"
                :page-sizes="[10,20, 50, 100, 200]"
                :total="page.totalRows"
                :size="page.pageSize"
                :current-page="page.currentPage"
                @size-change="sizeChange"
                @current-change="currentChange"
              >
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
    </div>

    <el-dialog title="添加密钥" :visible.sync="dialogFormVisible" :close-on-click-modal="false">
      <el-form :model="entity" status-icon :rules="rules" ref="form">
        <el-form-item label="名称:" :label-width="formLabelWidth" prop="auth_name">
          <el-input v-model="entity.auth_name" auto-complete="off" placeholder="请输入秘钥名称"></el-input>
        </el-form-item>
        <el-form-item label="APP:" :label-width="formLabelWidth" prop="app_id">
          <el-select v-model="entity.app_id" placeholder="请选择APP" style="width: 100%;">
            <el-option v-for="(item) in listApp"
                       :key="item.app_id"
                       :label="item.app_name"
                       :value="item.app_id"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="insert()">确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
/* eslint-disable*/
import {formatDate} from '../date.js'
import {Map} from '../map.js'
import {isEmpty} from '../isEmpty.js'
import {rownum} from '../arrayUtil.js'

  export default {
    name: 'AppManage',
    data () {
      return {
        page: this.config['page'],
        tableData: [],
        dialogFormVisible: false,
        formLabelWidth: '120px',
        dialogTableVisible: false,
        entity: {
          auth_name: '',
          app_id: ''
        },
        selected: [],
        listApp: [],
        rules: {
          auth_name: [
            { required: true, message: '请输入秘钥名称', trigger: 'blur' },
            { min: 1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur' }
          ],
          app_id: [
            { required: true, message: '请选择App', trigger: 'blur' }
          ]
        }
      }
    },
    mounted: function () {
      this.queryListForPage()
      this.appQueryList()
    },
    methods: {
      queryListForPage: function (func) {
        this.$http.post(this.api['authQueryListForPage'], {page: this.page}).then(function (response) {
          let data = response.data
          if (data.code === 0) {
            rownum(data.data)
            this.tableData = data.data
            this.page = data.page
          } else if (data.code !== 204) {
            this.$alert(data.msg, '提示', {
              confirmButtonText: '确定'
            })
          }
          if (func) {
            func(response)
          }
        }, function () {
          this.$message.error('后台异常')
        })
      },
      appQueryList: function(){
        this.$http.post(this.api['appQueryList'], {page: this.page}).then(function (response) {
          let data = response.data
          if (data.code === 0) {
            this.listApp = data.data
          } else if (data.code !== 204){
            this.$alert(data.msg, '提示', {
              confirmButtonText: '确定'
            })
          }
        }, function () {
          this.$message.error('后台异常')
        })
      },
      sizeChange: function (size) {
        this.page.pageSize = size
        this.page.currentPage = 1
        this.queryListForPage()
      },
      currentChange: function (begin) {
        this.page.currentPage = begin
        this.queryListForPage()
      },
      timeFormat (row, column, cellValue) {
        if (isEmpty(cellValue)) {
          return ''
        }
        return formatDate(new Date(cellValue), 'yyyy-MM-dd HH:mm:ss')
      },
      insert: function(){
        let self = this
        this.$refs['form'].validate((valid) => {
          if (!valid) {
            return
          }
        })
        if (isEmpty(this.entity.auth_name) || isEmpty(this.entity.app_id) ){
          this.$message.error('请填写秘钥名和选择APP')
          return
        }

        this.$http.post(this.api['authInsertOrUpdate'], this.entity).then(function (response) {
          let data = response.data
          if (data.code === 0) {
            this.dialogFormVisible = false
            this.queryListForPage()
          } else if (data.code !== 204){
            this.$alert(data.msg, '提示', {
              confirmButtonText: '确定'
            })
          }
        }, function (error) {
          this.$message.error('后台异常')
        })
      },
      changeFun: function(val){
        this.selected = val
      },
      remove: function() {
        this.$http.post(this.api['authDelete'], this.selected).then(function (response) {
          let data = response.data
          if (data.code === 0) {
            this.$message({
              message: '成功',
              type: 'success'
            });
            this.queryListForPage()
          } else if (data.code !== 204){
            this.$alert(data.msg, '提示', {
              confirmButtonText: '确定'
            })
          }
        }, function () {
          this.$message.error('后台异常')
        })
      },
      reflesh: function(){
        let self = this
        this.queryListForPage(function(){
          self.$message('刷新成功')
        })
      },
      clearObj: function (o) {
        if (o === undefined) {
          return
        }
        for (var p in o) {
          o[p] = ''
        }
        return o
      },
    }
  }
</script>

<style scoped>

</style>



