  mounted: function () {
    initNavigation()
    this.getCurrentUser()
  },
  methods: {
    loginout: function () {
      let self = this
      this.$http.get(this.api['authLoginOut']).then(function (response) {
        self.clearCookie()
        self.$router.push('/login')
      }, function (response) {
        self.clearCookie()
        // self.$message.error('后台异常')
      })
    },
    clearCookie(){
      this.removeCookie('JSESSIONID')
      this.removeCookie('name')
      this.removeCookie('email')
    },
    getCurrentUser: function () {
      this.name = decodeURI(this.getCookie('name'))
    }
  }
}
