<template>
  <div  class="sticky-header">
    <section class="login-section">
      <div class="container">
        <div class="row">
          <div class="login-wrapper">
            <div class="login-inner">

              <div class="logo">
                <img src="static/assets/images/logo-dark.png" alt="logo" />
              </div>

              <h2 class="header-title text-center">登录</h2>

              <form>
                <div class="form-group">
                  <input type="text" class="form-control" placeholder="用户名"  v-model="entity.email">
                </div>

                <div class="form-group">
                  <input type="password" class="form-control" placeholder="密码" v-model="entity.password">
                </div>

                <div class="form-group">
                  <div class="pull-left">
                    <div class="checkbox primary">
                      <input id="checkbox-2" type="checkbox">
                      <label for="checkbox-2">记住我</label>
                    </div>
                  </div>

                  <!--<div class="pull-right">
                    <a href="reset-password.html" class="a-link">
                      <i class="fa fa-unlock-alt"></i> 忘记密码?
                    </a>
                  </div>-->
                </div>

                <div class="form-group">
                  <input type="button" @click="submit()" value="登录" class="btn btn-primary btn-block">
                </div>

                <!--<div class="form-group text-center">
                  没有账号?
                  <a href="registration.html">不能注册 </a>
                </div>-->

              </form>

              <div class="copy-text">
                <p class="m-0">2019 &copy; 仪表管理员</p>
              </div>

            </div>
          </div>

        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      entity: {
        email: '',
        password: ''
      }
    }
  },
  mounted: function () {

  },
  methods: {
    submit: function () {
      if (this.entity.email === '') {
        this.$message.error('请输入账号(邮箱)')
        return
      }
      if (this.entity.password === '') {
        this.$message.error('请输入密码')
        return
      }
      this.login()
    },
    login: function () {
      var self = this
      this.$http.post(this.api['authLogin'], this.entity, {emulateJSON: true}).then(function (response) {
        if (response.data.code === 0) {
          self.$router.push('/')
        } else {
          this.$message.error('服务器认证失败 ' + response.data.msg)
        }
      }, function (response) {
        this.$message.error('服务器认证失败')
      })
    }
  }
}
</script>

<style scoped>

</style>



