export function rownum (arr) {
  for (let i = 0; i < arr.length; i++) {
    let obj = arr[i]
    obj.rownum = i + 1
  }
}
/* eslint-disable */
export function clearObj (o) {
  if (o === undefined) {
    return
  }
  for (var p in o) {
    o[p] = ''
  }
  return o
}



