const rule = /[a-z]/i

var host = 'http://' + window.location.hostname + ':8081'

if (rule.test(window.location.hostname) && window.location.hostname !== 'localhost') {
  host = 'http://' + window.location.host // 正式环境 用域名访问, 包含字母 除 localhost
} else {
  host = 'http://' + window.location.hostname + ':8081'// 开发环境 用ip访问
}

export const api = {
  // 登录 注销
  'authLogin': host + '/login',
  'authLoginOut': host + '/loginOut',
  // app 管理
  'appQueryListForPage': host + '/app/queryListForPage',
  'appInsertOrUpdate': host + '/app/insertOrUpdate',
  'appDelete': host + '/app/delete',
  // auth管理
  'authQueryListForPage': host + '/auth/queryListForPage',
  'authInsertOrUpdate': host + '/auth/insertOrUpdate',
  'authDelete': host + '/auth/delete',
  'appQueryList': host + '/app/queryList',
  // 用户管理
  'userQueryListForPage': host + '/user/queryListForPage',
  'userInsertOrUpdate': host + '/user/insertOrUpdate',
  'userDelete': host + '/user/delete'
}



