<template>
  <div>
    <div class="page-title-box">
      <h4 class="page-title">用户管理 </h4>
      <ol class="breadcrumb">
        <li>
          <button type="button" class="btn btn-success round" @click="dialogFormVisible = true,clearObj(entity)">新增</button>
        </li>

        <li >
          <button type="button" class="btn btn-danger round" @click="remove()">删除</button>
        </li>
        <li >
          <button type="button" class="btn btn-info round"  @click="reflesh()">刷新</button>
        </li>
      </ol>
      <div class="clearfix"></div>
    </div>

    <div class="row">
      <div class="col-md-12">
        <div class="white-box">
          <h2 class="header-title">Auth列表</h2>
          <div class="table-responsive">
            <template>
              <el-table
                :data="tableData"
                border
                style="width: 100%"
                size="mini"
                @selection-change="changeFun"
              >
                <el-table-column
                  type="selection"
                  width="50"
                  align="center">
                </el-table-column>
                <el-table-column
                  prop="rownum"
                  label="序号"
                  width="50"
                  align="center"
                >
                </el-table-column>
                <el-table-column
                  prop="user_name"
                  label="用户名"
                  width="80"
                  align="center"
                >
                </el-table-column>
                <el-table-column
                  prop="email"
                  align="center"
                  width="140"
                  label="邮箱">
                </el-table-column>
                <el-table-column
                  prop="founder"
                  align="center"
                  width="100"
                  label="创建人">
                </el-table-column>
                <el-table-column
                  prop="create_time"
                  align="center"
                  width="140"
                  label="创建时间"
                  :formatter="timeFormat">
                </el-table-column>
                <el-table-column
                  prop="update_time"
                  align="center"
                  width="140"
                  label="更新时间"
                  :formatter="timeFormat">
                </el-table-column>
                <el-table-column
                  prop="last_login_time"
                  align="center"
                  width="140"
                  label="上次登录时间"
                  :formatter="timeFormat">
                </el-table-column>
                <el-table-column
                  prop="last_login_ip"
                  align="center"
                  width="150"
                  label="上次登录IP">
                </el-table-column>
                <el-table-column
                  prop="login_times"
                  align="center"
                  width="50"
                  label="登录次数">
                </el-table-column>
                <el-table-column
                  align="center"
                  label="操作"
                  show-overflow-tooltip>
                  <template slot-scope="scope">
                    <button type="button" class="btn btn-warning round" @click="edit(scope.row)">编辑</button>
                  </template>
                </el-table-column>
              </el-table>
            </template>
            <div style="text-align: center;">
              <el-pagination
                background
                layout="sizes,prev, pager, next,total"
                :page-sizes="[10,20, 50, 100, 200]"
                :total="page.totalRows"
                :size="page.pageSize"
                :current-page="page.currentPage"
                @size-change="sizeChange"
                @current-change="currentChange"
              >
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
    </div>

    <el-dialog title="新增用户" :visible.sync="dialogFormVisible" :close-on-click-modal="false">
      <el-form :model="entity" status-icon :rules="rules" ref="form">
        <el-form-item label="用户名:" label-width="120px" prop="user_name">
          <el-input v-model="entity.user_name"  auto-complete="off" placeholder="请输入用户名"></el-input>
        </el-form-item>
        <el-form-item label="邮箱:" label-width="120px" prop="email">
          <el-input v-model="entity.email" auto-complete="off" placeholder="请输入邮箱"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="insert()">确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
/* eslint-disable*/
import {formatDate} from '../date.js'
import {Map} from '../map.js'
import {isEmpty} from '../isEmpty.js'
import {rownum} from '../arrayUtil.js'

  export default {
    name: 'UserManage',
    data () {
      return {
        page: this.config['page'],
        tableData: [],
        dialogFormVisible: false,
        dialogTableVisible: false,
        entity: {
          user_name: '',
          email: ''
        },
        selected: [],
        listApp: [],
        rules: {
          user_name: [
            { required: true, message: '请输入用户名', trigger: 'blur' },
            { min: 1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur' }
          ],
          email: [
            { type: 'email', required: true, message: '请输入合法邮箱', trigger: 'change' }
          ]
        }
      }
    },
    mounted: function () {
      this.queryListForPage()
    },
    methods: {
      queryListForPage: function (func) {
        this.$http.post(this.api['userQueryListForPage'], {page: this.page}).then(function (response) {
          let data = response.data
          if (data.code === 0) {
            rownum(data.data)
            this.tableData = data.data
            this.page = data.page
          } else if (data.code !== 204) {
            this.$alert(data.msg, '提示', {
              confirmButtonText: '确定'
            })
          }
          if (func) {
            func(response)
          }
        }, function () {
          this.$message.error('后台异常')
        })
      },
      sizeChange: function (size) {
        this.page.pageSize = size
        this.page.currentPage = 1
        this.queryListForPage()
      },
      currentChange: function (begin) {
        this.page.currentPage = begin
        this.queryListForPage()
      },
      timeFormat (row, column, cellValue) {
        if (isEmpty(cellValue)) {
          return ''
        }
        return formatDate(new Date(cellValue), 'yyyy-MM-dd HH:mm:ss')
      },
      insert: function(){
        let self = this
        this.$refs['form'].validate((valid) => {
          if (valid) {
            self.$http.post(self.api['userInsertOrUpdate'], self.entity).then(function (response) {
              let data = response.data
              if (data.code === 0) {
                self.dialogFormVisible = false
                self.queryListForPage()
              } else if (data.code !== 204) {
                self.$alert(data.msg, '提示', {
                  confirmButtonText: '确定'
                })
              }
            }, function (error) {
              console.log('error submit!!')
              self.$message.error('后台异常')
            })
          } else {
            return false;
          }
        })
      },
      changeFun: function(val){
        this.selected = val
      },
      remove: function() {
        this.$http.post(this.api['userDelete'], this.selected).then(function (response) {
          let data = response.data
          if (data.code === 0) {
            this.$message({
              message: '成功',
              type: 'success'
            });
            this.queryListForPage()
          } else  if (data.code !== 204) {
            this.$alert(data.msg, '提示', {
              confirmButtonText: '确定'
            })
          }
        }, function () {
          this.$message.error('后台异常')
        })
      },
      reflesh: function(){
        let self = this
        this.queryListForPage(function(){
          self.$message('刷新成功')
        })
      },
      edit: function (row) {
        this.entity = row
        this.dialogFormVisible = true
      },
      clearObj: function (o) {
        if (o === undefined) {
          return
        }
        for (var p in o) {
          o[p] = ''
        }
        return o
      }
    }
  }
</script>

<style scoped>

</style>



