<template>
  <div>
    <div class="page-title-box">
      <h4 class="page-title">Dashboard页： Elves各模块健康状况 </h4>
      <div class="clearfix"></div>
    </div>

    <div class="row">
      <div class="col-md-12">
        <div class="white-box">
          <div class="table-responsive">
            <div  id="elvesModule" style="height:500px;width:1100px;float:left;position:relative;"></div>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
/* eslint-disable */
import {formatDate} from '../date.js'
import {Map} from '../map.js'
import {isEmpty} from '../isEmpty.js'
import {rownum, clearObj} from '../arrayUtil.js'

  export default {
    name: 'Dashboard',
    data () {
      return {

      }
    },
    mounted: function () {
      this.queryAll()
      initDashboard()
    },
    methods: {
      queryListForPage: function (func) {
        this.$http.post(this.api['userQueryListForPage'], {page: this.page}).then(function (response) {
          let data = response.data
          if (data.code === 0) {
            rownum(data.data)
            this.tableData = data.data
            this.page = data.page
          } else if (data.code !== 204) {
            this.$alert(data.msg, '提示', {
              confirmButtonText: '确定'
            })
          }
          if (func) {
            func(response)
          }
        }, function () {
          this.$message.error('后台异常')
        })
      },
      queryAll: function () {

      }
    }
  }
</script>

<style scoped>

</style>



