import Vue from 'vue'
import App from './App'
import router from './router'
import VueResource from 'vue-resource'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import { api } from './api'

// css 等静态资源
import '../static/assets/css/bootstrap.min.css'
import '../static/assets/css/icons.css'
import '../static/assets/css/responsive.css'
import '../static/assets/css/style.css'
import { config } from './config'

Vue.use(router)
Vue.use(VueResource)
Vue.use(ElementUI)

Vue.config.productionTip = false
Vue.prototype.api = api
Vue.prototype.config = config

Vue.http.options.xhr = { withCredentials: true }

Vue.prototype.getCookie = function (key) {
  let reg = new RegExp('(^| )' + key + '=([^;]*)(;|$)')
  let arr = document.cookie.match(reg)
  if (arr) {
    return arr[2]
  } else {
    return null
  }
}

Vue.prototype.setCookie = function (key, value, hours) {
  let d = new Date()
  d.setTime(d.getTime() + (hours * 60 * 60 * 1000))
  let expires = 'expires=' + d.toUTCString()
  document.cookie = key + '=' + value + '; ' + expires
}

Vue.prototype.removeCookie = function (key) {
  this.setCookie(key, '', -1)
}

// 访问路由前
router.beforeEach((to, from, next) => {
  let result = to.path === '/login'
  if (!result) {
    let jssion = Vue.prototype.getCookie('JSESSIONID')
    if (!jssion) {
      next({
        path: '/login'
      })
    } else {
      next()
    }
  } else {
    next()
  }
})

// 拦截器判断是否有token认证
Vue.http.interceptors.push((request, next) => {
  request.credentials = true
  next(response => {
    if (response.data.code === 204) {
      router.push({ path: '/login' })
    }
    return response
  })
})
/* eslint-disable */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



